.hero--about {
  display: block;
  background: $blue-very-dark;

  color: white;

  &__inner {
    @include inner-container;
    display: grid;
    grid-template-columns: 3fr 1fr;
    border: 0px solid #000;
    height: 100vh;
    padding: 0;
    text-align: left;
  }

  &__content {
    padding: 0 2rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    @include position;
    z-index: 3;

    & > div {
      margin-bottom: 3rem;
    }
  }


  &__title {
    h1,
    h1 p {
      font-family: $playfair;
      font-weight: 900;
      color: white;
      font-size: 42px;
      // margin-left: 2rem;
      line-height: 140%;
    }
  }

  &__subtitle {
    font-family: $noto;
    font-size: 12px;
    font-weight: 300;
    font-style: italic;
    line-height: 160%;
    color: white;
    font-size: 12px;
    @include position;

    &:before {
      @include before-line-small;
      background-color: white;
    }
  }

  &__content {
    padding: 0 2rem;

    .hero--about__content {
      padding: 0;

      @include mq('tablet', 'min') {
        padding: 0 2rem;
      }
    }

    > p {
      @include paragraph;
      color: white;
      font-weight: 300;


      @include mq('tablet', 'min') {
        width: 75%;
      }
    }
    @include mq('tablet', 'min') {
      padding: 0 2rem;
    }
  }
}

.logo-detail--xl {
  background: #fff;
  padding: 16px 0;

  svg {
    width: 100%;
    height: auto;
  }
}

.logo-detail-about--xl {
  padding: 0;
  max-height: 710px;
  overflow: hidden;

  svg {
    width: 103%;
    margin-left: -3%;
    height: auto;
  }
}

.llogo-detail-about--xl {
  background: white;
  figure {
    margin-bottom: 0;
  }
  svg {
    max-width: 100%;
    height: auto;
  }
}