.page-template-page-contact {
  .wp-block-group {
    &__inner-container {
      @include inner-container;
    }

    h2 {
      @include heading-h2;
      font-family: $playfair;
      font-style: italic;
      color: $pink;
      margin-left: 3rem;
    }

    p {
      @include paragraph;
      width: 90%;
    }

    .wpforms-container-full {
      margin-top: 3rem;
    }

    .wpforms-field {
      input[type="text"],
      input[type="email"],
      textarea {
        border: 0;
        background: darken(white, 5%);
        border-radius: 3px;
        padding: 1.5rem;
        width: 620px;

        @include mq('tablet','min') {
          margin-left: 3rem;
        }

        &::placeholder {
          font-family: $noto;
          font-style: italic;
          font-weight: 300;
          color: #333;
        }
      }

    }
  }
}

div.wpforms-container-full .wpforms-form button[type=submit].btn {
  margin-top: 3rem;
  background: white;
  padding: 1.2rem 2rem;
  border-radius: 40px;
  box-shadow: 0px 0px 35px rgba(0, 0, 0, 0.19);
  font-family: $noto;
  font-size: 1rem;
  font-weight: 300;
  text-decoration: none;
  color: $blue-dark;
  display: inline-block;
  font-style: italic;
  @include position;
  @include transition(all, 0.4s);

  &:hover {
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  }

  &--small {
    padding: 1rem 2rem 1rem 2.6rem;
  }

  .icon {
    @include position(absolute, 1rem, 50%);
    width: $icon-size;
    height: $icon-size;
    transform: translateY(-9px);

    svg {
      width: 100%;
      height: 100%;
    }
  }

  .text {
    @include position;
    top: 1px;
  }
}

/* ----------------------------------------
  DETAILS COLUMN
---------------------------------------- */

.contact-details-column {
  padding: 3rem;

  @include mq('tablet','min') {
    padding: 0;
  }

  h4 {
    font-style: italic;
    font-family: $noto;
    color: $pink;
    line-height: 200%;
    margin-top: 1rem;
    margin-bottom: 3rem;
  }

  ul {
    font-style: italic;
    font-weight: 300;
    color: lighten(black, 50%);
    font-family: $noto;

    li {
      margin-bottom: 1rem;
      @include position;

      a {
        color: lighten(black, 40%);
        text-decoration: none;

        &:hover {
          color: lighten(black, 10%);
        }
      }

      &.telephone {
        &:before {
          background-image: url('./../../assets/icons/feather-icons/phone.svg');
        }
      }

      &.mail {
        &:before {
          background-image: url('./../../assets/icons/feather-icons/mail.svg');
        }
      }

      &.hash {
        &:before {
          background-image: url('./../../assets/icons/feather-icons/hash.svg');
        }
      }


      &:before {

        @include position(absolute, -24px, 2px);
        content: "";
        width: 24px;
        height: 24px;
        background-size: 50%;
        opacity: 0.6;
        background-repeat: no-repeat;
      }
    }
  }
}