.hero {
  min-height: 100vh;
  width: 100vw;
  background: #AED0D2;
  display: flex;
  align-items: center;
  @include position;
  @include transition(all, 0.5s, $default-easing);
  padding-top: 10rem;

  @include mq('tablet','min') {
    padding-top: 0rem;
  }


  &.make-visible {
    @include mq('tablet','min') {
      height: calc(100vh - 2rem);
      overflow: hidden;
    }

    .single-portfolio & {
      height: calc(200vh);

      @include mq('tablet', 'min') {
        height: calc(100vh);
      }
    }
  }


  &__background {
    @include position(absolute, 0, 0);
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__inner {
    @include position;
    z-index: 3;
    max-width: 71rem;
    width: 100%;
    padding: 0 2rem;
    margin: 0 auto;
    height: 610px;


    @include mq('tablet','min') {
      padding: 0;
    }
  }
}